.header{
  display: flex;
  background-color: white;
  padding: 10px;
  justify-content: space-between;
 align-items:center;

 flex: 1;
 position: sticky;
 top: 0px; 
 z-index: 100;
 
}
.header__logo{
  color: red;
  font-size: 50px;
   margin-left: 5px;
margin-right: 3px;

}



.header__input{

  height: 35px;
width: 40vw;
min-width: 100px;
border: none;
}
.header__containerLogo{
display: flex;

justify-content: center;
align-items: center;
}
.header__threeBar{
font-size: 5vw;
}

.header__search{
  display: flex;
  border: 1px solid black; 
  justify-content: center;
  align-items: center;
  margin-left: 5px;
}
.header__searchIcon{
font-size: 30px;
width: 50px;
background-color: #f6f6ff;
border-left: 1px solid gray;

}

.header__right{
 display: flex;
align-items: center;
justify-content: space-between;

}

.header-icon{
margin-left: 5px;
}